<template>
  <div class="hx-row" :class="[
    oneline && 'oneline',
    !label && 'null-label',  // label content 
    labelSize && `${labelSize}-label`]">
    <label v-if="label">
      {{ label }}
      <span v-if="required" class="tip">*</span>
      <span class="label-tips" v-text="tips" v-if="tips"></span>
      <slot name="label"></slot>
    </label>
    <div class="content">
      <slot ></slot>
      <span class="right">
        <slot name="right"></slot>
        <slot name="degree"></slot> 
      </span>
    </div>
    <span class="text-tips">
      <slot name="tips"></slot>
    </span>
  </div>
</template>

<script>
/**
 * HxRow 3
 *  label label 
 *  right  
 *  tips 
 */
export default {
  name: 'hx-row',
  props: {
    label: {
      type: String
    },
    labelSize: { //  null md, bg
      type: String,
      validator (val) {
        return ['md', 'bg', 'sm', 'lg'].includes(val)
      }
    },
    tips: {
      type: String // label
    },  
    oneline: {
      type: [String, Boolean, Number],
      default: false
    },
    required: {
      type: [String, Boolean, Number],
      default: false
    }
  },
  data () {
    return {}
  },
  created () {}
}
</script>
