<template>
  <div class="hx-tag-editor">
    <div class="zone-tags">
      <div v-for="(item, idx) in target" :class="['tag', disabled && 'disabled']" :key="idx">
        {{ item }}
        <button class="btn-remove" v-if="!disabled && !readonly" @click="doRemoveTag(idx)"></button>
      </div>
      <input 
        :placeholder="placeholder" 
        v-model="content"
        type="text" 
        class="tag-inputer"
        v-if="!disabled && !readonly"
        @keydown="doCreateTag" />
    </div>
  </div>
</template>
<script>
import toast from './../plugins/toast'
// 
export default {
  data () {
    return {
      hasInitiated: false,
      isString: false, //  value 
      content: '',
      target: ''
    }
  },
  props: {
    value: {
      type: [Array, String],
      required: true
    },
    splitChar: { // 
      type: String,
      default: '|'
    },
    placeholder: {
      type: String,
      default: ''
    },
    noRepeat: {
      type: [String, Number, Boolean],
      default: false
    },
    disabled: {
      type: [Boolean, String, Number],
      default: false
    },
    readonly: {
      type: [Boolean, String, Number],
      default: false
    },
    onCreated: { // 
      type: Function
    },
    onRemoved: { // 
      type: Function
    }
  },
  methods: {
    $_initTarget () {
      if (Array.isArray(this.value)) {
        this.target = [].concat(this.value)
        return
      }
      this.isString = true
      if (!this.value) {
        this.target = []
      } else {
        this.target = this.value.split(this.splitChar)
      }
    },
    $_syncTarget () {
      if (this.isString) {
        this.$emit('input', this.target.join(this.splitChar))
      } else {
        this.$emit('input', this.target)
      }
    },
    $_isAnySameTag () {
      return this.target.filter(v => v === this.content).length
    },
    doCreateTag () {
      if (!this.content && event.keyCode === 8) { 
        // 
        this.doRemoveTag(this.target.length - 1)
        return
      }
      if (!this.content || event.keyCode !== 13) {
        return
      }
      if (!this.content.trim()) {
        this.content = ''
        return
      }
      if (this.noRepeat && this.$_isAnySameTag()) {
        toast.warn('')
        return
      }
      this.target.push(this.content)
      this.onCreated instanceof Function && this.onCreated(this.content)
      this.$emit('change', this.content)
      this.content = ''
      this.$_syncTarget()
    },
    doRemoveTag (idx) {
      const tag = this.target[idx]
      this.target.splice(idx, 1)
      this.onRemoved instanceof Function && this.onRemoved(tag)
      this.$emit('change', tag)
      this.$_syncTarget()
    }
  },
  created () {
    this.$_initTarget()
  },
  watch: {
    value: {
      deep: true,
      handler (newVal) {
        if (newVal) {
          this.$_initTarget()
        }
      }
    }
  }
}
</script>
